.game-info {
    border-radius: 25px;
    padding: 20px;
}

tfoot {
    font-family: 'Roboto Condensed', 'Helvetica', 'Arial', sans-serif;
    border-bottom: 1px solid #ddd;
    border-collapse: collapse;
    border-spacing: 0;
    font-size: .8125rem;
    margin-bottom: 0;
    max-width: 100%;
    width: 100%;
    background-color: transparent;
    display: table-footer-group;
    vertical-align: middle;
    border-color: inherit;
}

.nba-stat-table .player-headshot {
    border: 1px solid #efefef;
    border-radius: 50%;
    display: inline-block;
    float: left;
    height: 40px;
    margin-right: 10px;
    overflow: hidden;
    overflow-x: hidden;
    overflow-y: hidden;
    width: 40px;
}

.nba-stat-table .player-headshot img {
    background: #fff;
    display: inline-block;
    height: 40px;
    margin: 0 -7px;
    max-width: 54px;
    width: 54px;
}

.nba-stat-table__caption {
    background-image: initial;
    background-color: #006bb6;
    font-family: 'Roboto Condensed', 'Helvetica', 'Arial', sans-serif;
    padding: 0.5em;
    color: #fff;
    font-size: 16px;
    display: block;
    text-align: left;
    width: 100%;
    min-width: 800px;
}


.nba-stat-table__caption a {
    color: #fff;
}

.nba-stat-table__caption a:hover {
    color: rgb(118, 194, 245);
}

.nba-stat-table {
    display: block;
    font-size: 11px;
    margin-bottom: 1em;
    font-weight: 400;
    position: relative;
    color: #000;
}

.nba-stat-table table {
    font-family: 'Roboto Condensed', 'Helvetica', 'Arial', sans-serif;
    border-collapse: collapse;
    border-spacing: 0;
    font-size: .8125rem;
    margin-bottom: 0;
    width: 100%;
    /* background-color: transparent;
    border-bottom: 1px solid #ddd; */
    border-bottom-color: #fff;
    background-color: transparent;
    overflow: auto;
    table-layout: fixed;
    min-width: 800px;
}

.nba-stat-table table thead tr th {
    background-image: initial;
    color: #fff;
    border-bottom-color: initial;
    background: #032f4f;
    line-height: 1.15em;
    padding: 13px 6px 9px;
    vertical-align: bottom;
    z-index: 20;
    border-bottom: 0;
    text-transform: uppercase;
    font-size: .6875rem;
    letter-spacing: 1px;
    font-weight: 500;
    white-space: nowrap;
}

.nba-stat-table table tbody tr td {
    border-top-color: initial;
    border-right-color: initial;
    border-bottom-color: initial;
    border-left-color: initial;
    font-weight: 500;
    position: relative;
    border: 0;
    padding: 9px 6px;
}
.nba-stat-table table tbody tr td.player, .nba-stat-table table tfoot tr td.player, .nba-stat-table table thead tr th.player {
    text-align: left;
    min-width: 150px;
    width: 154px;
}
.nba-stat-table table tfoot tr td {
    border-top-color: initial;
    border-right-color: initial;
    border-bottom-color: initial;
    border-left-color: initial;
    font-weight: 500;
    position: relative;
    border-top: 0.5px solid rgb(133, 133, 133);
    border-bottom: 0.5px solid rgb(133, 133, 133);
    padding: 9px 6px;
    background-color: #fff;
}

.nba-stat-table td a {
    color: rgb(5, 46, 75);
}

.nba-stat-table tbody tr:nth-child(2n) {
    background-color: #f4f4f4;
}

.nba-stat-table tbody tr:nth-child(2n+1) {
    background-color: #fff;
}

.game-view {
    position: relative;
}
