.game-headers {
    width: 100%;
    font-family: 'Roboto Condensed', 'Helvetica', 'Arial', sans-serif;
    position: relative;
    display: block;
    padding-bottom: 10px;
}

.game-headers .game-hero {
    height: 136px;
    position: relative;
    background: #44a0e2;
    font-size: 0;
    padding: 22px 15px;
    color: #fff;
}

.game-headers .game-active:hover {
    background: #cedeeb;
    cursor: pointer;
}

.game-headers .game-not-active:hover {
    cursor: default;
}