.game-away {
    width: 30%;
    height: 100%;
    vertical-align: top;
    display: inline-block;
    text-align: right;
}

.game-home {
    width: 30%;
    height: 100%;
    vertical-align: top;
    display: inline-block;
    text-align: left;
}

.game-away .team-name, .game-home .team-name {
    width: auto;
    font-size: 18px;
    color: white;
    text-shadow: 1px 1px black;
    display: block;
    font-weight: 600;
    line-height: 5.5;
}

.game-wrapper-logo {
    position: relative;
    width: 118px;
    height: 100%;
    display: inline-block;
}

.home-logo {
    float: left;
}

.away-logo {
    float: right;
}

.team-logo {
    width: 100%;
    height: 100%;
    display: inline-block;
    background-size: contain;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center center;
}

.team-logo img {
    width:100%;
    height:100%;
}