.info-wrap {
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
    color: #fff;
    text-align: center;
    font-size: 0;
}

.game-hero .game-info {
    display: inline-block;
    vertical-align: top;
    position: relative;
    height: 100%;
    width: 40%;
    color: #fff;
    text-align: center;
    font-size: 0;
    padding: 0;
}

.game-hero .pre-game {
    width: 15%;
}

.pre-game-text {
    width: 100% !important;
}

.game-live .away-score, .game-live .home-score, .game-live .game-state, .game-post .away-score, .game-post .home-score, .game-post .game-state {
    display: inline-block;
    vertical-align: middle;
    font-size: 58px;
    width: 30%;
    overflow: hidden;
}

.final {
    font-size: 20px;
    color: white;
    display: inline-block;
    vertical-align: middle;
    width: 30%;
    overflow: hidden;
}

.game-state {
    font-size: 20px;
    width: 30%;
}

.game-live .game-hero .game-result, .game-post .game-hero .game-result {
    color: white;
    font-size: 16px;
    font-weight: 200;
}

.game-quarter, .game-countdown {
    font-size: 20px;
    color: #ff0a45;
    font-weight: 700;
}